:root{
    --primary:#fff;
}
.btn{
    padding: 8px 20px;
    border-radius:2px;
    outline:none;
    border:none ;
    cursor: pointer;
}
.btn--primary{
    background-color:white ;
    color:black;
    border:1px solid var(--primary);

}
.btn--outline{
    background-color:transparent ;
    color:white;
    padding: 8px 20px;
    border:1px solid var(--primary);  
    transition:all 0.3s ease-out;
}
.btn--medium{
    padding: 8px 20px;
    font-size: 20px;
}
.btn--large{
    padding:12px 20px;
    font-size: 20px;
}

.btn--large:hover,
.btn--medium:hover {
  transition: all 0.3s ease-out;
  background: #fff;
  color: #242424;
  transition: 250ms;
}

















